const swiper = new Swiper('.header-swiper', {
    loop: true,
    Parallax: true,
    speed: 1000,
    Keyboard: {
        enable: true
    },

    // If we need pagination
    pagination: {
        el: '.swiper-pagination',
        clickable: true
    },
});

const swiper_advantages = new Swiper('.advantages-swiper', {
    speed: 500,
    slidesPerView: 1,
    spaceBetween: 24,
    navigation: {
        nextEl: '.advantages__button-next',
        prevEl: '.advantages__button-prev'
    }
});
const swiper_trust = new Swiper('.trust__swiper-first', {
    loop: true,
    speed: 500,
    slidesPerView: 1,
    spaceBetween: 24,
    navigation: {
        nextEl: '.trust__button-next',
        prevEl: '.trust__button-prev'
    },
    breakpoints: {
        400:{
            slidesPerView: 1.5
        },
        600:{
            slidesPerView: 2
        },
        900:{
            slidesPerView: 3
        },
        1120: {
            slidesPerView: 4
        },

    }
});

const swiper_trust_second = new Swiper('.trust__swiper-second', {
    loop: true,
    speed: 500,
    slidesPerView: 2,
    breakpoints: {
        400:{
            slidesPerView: 3
        },
        600:{
            slidesPerView: 3
        },
        900:{
            slidesPerView: 3
        },
        1120: {
            slidesPerView: 4
        },

    },
    spaceBetween: 24,
    navigation: {
        nextEl: '.trust__button-next',
        prevEl: '.trust__button-prev'
    }
});
const swiper_trust_third = new Swiper('.trust__swiper-third', {
    speed: 500,
    slidesPerView: 1,
    breakpoints: {
        400:{
            slidesPerView: 1.3
        },
        600:{
            slidesPerView: 2
        },
        900:{
            slidesPerView: 3
        },
        1120: {
            slidesPerView: 4
        },

    },
    spaceBetween: 24,
});

const menuBtn = document.querySelector('.menu__btn');
const menuMobile = document.querySelector('.header__mobile-menu')

menuBtn.addEventListener('click', () => {
    menuBtn.classList.toggle('active'),
        menuMobile.classList.toggle('menu--open'),
        document.body.classList.toggle('lock')
})